"use client"

import { useState } from "react"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"

export default function SignupForm() {
  const [step, setStep] = useState(1)

  const [formData, setFormData] = useState({
    name: "",
    shopName: "",
    phone: "",
    email: "",
    password: "",
    confirmPassword: "",
  })

  const [errors, setErrors] = useState({})
  const [isLoading, setIsLoading] = useState(false)

  const handleChange = (e) => {
    const { name, value } = e.target
    setFormData((prev) => ({ ...prev, [name]: value }))
    if (errors[name]) setErrors((prev) => ({ ...prev, [name]: "" }))
  }

  const validateStep1 = () => {
    const newErrors = {}
    if (!formData.name.trim()) newErrors.name = "Name is required"
    if (!formData.shopName.trim()) newErrors.shopName = "Shop name is required"
    if (!formData.phone.trim()) newErrors.phone = "Phone is required"
    if (formData.phone.length < 10) newErrors.phone = "Invalid phone number"
    return newErrors
  }

  const validateStep2 = () => {
    const newErrors = {}
    if (!formData.email.trim()) newErrors.email = "Email is required"
    if (!formData.email.includes("@")) newErrors.email = "Invalid email"
    if (formData.password.length < 6) newErrors.password = "Password must be 6+ characters"
    if (formData.password !== formData.confirmPassword)
      newErrors.confirmPassword = "Passwords do not match"
    return newErrors
  }

  const handleNext = () => {
    const newErrors = validateStep1()
    if (Object.keys(newErrors).length === 0) {
      setStep(2)
      setErrors({})
    } else {
      setErrors(newErrors)
    }
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    const newErrors = validateStep2()
    if (Object.keys(newErrors).length > 0) return setErrors(newErrors)

    setIsLoading(true)
    setTimeout(() => {
      window.location.href = "/plans"
      setIsLoading(false)
    }, 1000)
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-white flex items-center justify-center">

      {/* FULL SCREEN GRID */}
      <div className="w-full grid grid-cols-1 md:grid-cols-2 min-h-screen">

        {/* LEFT ILLUSTRATION */}
        <div className="hidden md:flex flex-col items-center justify-center bg-blue-100 p-10">
          <img
            src="/signup-illustration.svg"
            alt="Signup Illustration"
            className="w-72 h-auto"
          />
          <h2 className="text-3xl font-bold text-blue-900 mt-6">Grow Your Shop</h2>
          <p className="text-blue-800 mt-3 text-center max-w-sm">
            Manage loyalty points, track customers, and grow your business smoothly.
          </p>
        </div>

        {/* RIGHT FORM */}
        <div className="flex items-center justify-center p-8 bg-white">
          <div className="w-full max-w-md">

            {/* HEADER */}
            <div className="mb-8">
              <h1 className="text-3xl font-bold text-blue-900 mb-2">
                {step === 1 ? "Create Account" : "Set Password"}
              </h1>
              <p className="text-gray-600">
                {step === 1
                  ? "Join thousands of shopkeepers managing loyalty"
                  : "Secure your account"}
              </p>
            </div>

            {/* PROGRESS BAR */}
            <div className="flex gap-2 mb-8">
              {[1, 2].map((s) => (
                <div
                  key={s}
                  className={`h-1 flex-1 rounded-full ${
                    s <= step ? "bg-blue-600" : "bg-gray-200"
                  }`}
                />
              ))}
            </div>

            {/* FORM */}
            <form onSubmit={handleSubmit}>
              {step === 1 ? (
                <div className="space-y-4">
                  
                  {/* FULL NAME */}
                  <div>
                    <label className="block text-sm font-medium text-blue-900 mb-2">
                      Full Name
                    </label>
                    <Input
                      type="text"
                      name="name"
                      placeholder="Your name"
                      value={formData.name}
                      onChange={handleChange}
                      className={errors.name ? "border-red-500" : "border-blue-200"}
                    />
                    {errors.name && (
                      <p className="text-red-600 text-sm mt-1">⚠️ {errors.name}</p>
                    )}
                  </div>

                  {/* SHOP NAME */}
                  <div>
                    <label className="block text-sm font-medium text-blue-900 mb-2">
                      Shop Name
                    </label>
                    <Input
                      type="text"
                      name="shopName"
                      placeholder="Your shop name"
                      value={formData.shopName}
                      onChange={handleChange}
                      className={errors.shopName ? "border-red-500" : "border-blue-200"}
                    />
                    {errors.shopName && (
                      <p className="text-red-600 text-sm mt-1">⚠️ {errors.shopName}</p>
                    )}
                  </div>

                  {/* PHONE */}
                  <div>
                    <label className="block text-sm font-medium text-blue-900 mb-2">
                      Phone Number
                    </label>
                    <Input
                      type="tel"
                      name="phone"
                      placeholder="+91 XXXXX XXXXX"
                      value={formData.phone}
                      onChange={handleChange}
                      className={errors.phone ? "border-red-500" : "border-blue-200"}
                    />
                    {errors.phone && (
                      <p className="text-red-600 text-sm mt-1">⚠️ {errors.phone}</p>
                    )}
                  </div>

                  <Button
                    onClick={handleNext}
                    className="w-full mt-6 bg-blue-600 hover:bg-blue-700 text-white"
                  >
                    Continue
                  </Button>
                </div>
              ) : (
                <div className="space-y-4">

                  {/* EMAIL */}
                  <div>
                    <label className="block text-sm font-medium text-blue-900 mb-2">
                      Email Address
                    </label>
                    <Input
                      type="email"
                      name="email"
                      placeholder="your@email.com"
                      value={formData.email}
                      onChange={handleChange}
                      className={errors.email ? "border-red-500" : "border-blue-200"}
                    />
                    {errors.email && (
                      <p className="text-red-600 text-sm mt-1">⚠️ {errors.email}</p>
                    )}
                  </div>

                  {/* PASSWORD */}
                  <div>
                    <label className="block text-sm font-medium text-blue-900 mb-2">
                      Password
                    </label>
                    <Input
                      type="password"
                      name="password"
                      placeholder="Minimum 6 characters"
                      value={formData.password}
                      onChange={handleChange}
                      className={errors.password ? "border-red-500" : "border-blue-200"}
                    />
                    {errors.password && (
                      <p className="text-red-600 text-sm mt-1">⚠️ {errors.password}</p>
                    )}
                  </div>

                  {/* CONFIRM PASSWORD */}
                  <div>
                    <label className="block text-sm font-medium text-blue-900 mb-2">
                      Confirm Password
                    </label>
                    <Input
                      type="password"
                      name="confirmPassword"
                      placeholder="Re-enter password"
                      value={formData.confirmPassword}
                      onChange={handleChange}
                      className={errors.confirmPassword ? "border-red-500" : "border-blue-200"}
                    />
                    {errors.confirmPassword && (
                      <p className="text-red-600 text-sm mt-1">
                        ⚠️ {errors.confirmPassword}
                      </p>
                    )}
                  </div>

                  {/* ACTIONS */}
                  <div className="flex gap-3 mt-6">
                    <Button
                      type="button"
                      variant="outline"
                      onClick={() => setStep(1)}
                      className="flex-1 border-blue-200 text-blue-600 hover:bg-blue-50"
                    >
                      Back
                    </Button>

                    <Button
                      type="submit"
                      className="flex-1 bg-blue-600 hover:bg-blue-700 text-white"
                      disabled={isLoading}
                    >
                      {isLoading ? "Creating..." : "Create Account"}
                    </Button>
                  </div>
                </div>
              )}
            </form>

            <p className="text-center text-sm text-gray-600 mt-6">
              Already have an account?{" "}
              <Link
                href="/login"
                className="text-blue-600 font-semibold hover:text-blue-700 hover:underline"
              >
                Sign in
              </Link>
            </p>

          </div>
        </div>
      </div>
    </div>
  )
}
